.serv a p{
    font-size: 14px ;
}
.serv a{
    width: 230px;
    display: flex;
    justify-content: space-between; /* Make sure the elements are in a row */
    align-items: center;
    text-decoration: none; /* Remove default link underline */
    padding-bottom: 10px; /* Add spacing between the links and the underline */
    border-bottom: 1px solid #000000; /* Add a 1px solid gray underline to each link */
    margin-bottom: 10px;
}

@media (max-width: 600px) {
    .nor {
        margin: 5%;
        padding:0rem;
        padding-top: 5rem;
        justify-content: center;
        align-items: center;
    }
    .serv{
        padding-left: 0%;
        padding-right: 0%;
        width: 0%;
        overflow: hidden;
        display: none;
    }
}
.relat{
    position: fixed;
    top: 100px; /* Adjust this value to position it below the navbar */
    z-index: 1;
}
@media (max-width:800px) {
    .serv a p{
        font-size: 10px ;
    }
    .serv a{
        width: 170px;
    }
    .nor img{
        object-fit: contain;
        padding: 5%;
    }
}
.serv a img{
    width: 10px;
    height: 10px;
    display: flex;
    object-fit: cover;
}

.serv{
    position: relative;
}

.styled-list li h1{
    margin: 0; /* Remove default margin for the heading */
    font-size: 15px;
    font-weight: bold;
}
.styled-list {
    list-style-type: disc;
    list-style-position: outside;
    padding-left: 25px;
}

.styled-list li {
    margin-bottom: 10px;
}

.description{
    display: flex;
    float: left;
}

.sty{
    list-style-type: disc;
    padding-left: 25px;
}