* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.new {
    border-bottom: 2px solid #225c59;
}

.Test-size {
    height: 45rem;
}

.feedback {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 12rem;
    gap: 0.6rem;

}

.Test-text {
    position: relative;
    font-size: 2.5rem;
    padding: 1rem;
    text-align: center;
    color: #ffff;
}

.ecllip3 {
    object-fit: cover;
    background-size: cover;
    position: relative;
    width: 10rem;
    height: 10rem;
    background-color: transparent;
    border-radius: 50%;
    margin: 0 10px;

}

.ecllip4 {
    object-fit: cover;
    position: relative;
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    background-color: transparent;
    margin: 70px 10px;
    background-size: cover;
    display: none;
}

.mona {
    position: relative;
    color: #ffff;
    text-align: center;
    font-size: 2.2rem;
    padding-top: 1rem;

}

.star {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem;
}

@media(max-width:1356px) {


    .ecllip4 {
        height: 6rem;
        width: 6rem;
    }

    .ecllip2 {
        height: 6rem;
        width: 6rem;
    }

}

@media(max-width:1445px) {
    .feedback {
        height: max-content;
    }

}

@media(max-width:698px) {
    .Test-text {
        font-size: 2.8rem;
        padding-bottom: 4rem;
        margin-bottom: 0;
    }

    .text-\[1rem\] {
        font-size: 0.7rem;
    }

    .Test-size {
        height: 55rem;
    }

    .ecllip3 {
        height: 12rem;
        width: 12rem;
    }

    .ecllip1,
    .ecllip2,
    .ecllip4,
    .ecllip5 {
        visibility: hidden;
        width: 0;
        margin: 0;
    }

    .feedback {
        height: auto;
        padding: 0;
        margin: 0;
        /* margin: 2rem 0; */
    }

    .mona {
        font-size: 1.4rem;
        margin-top: 1rem;
    }


    @media(max-width:500px) {
        .Test-size {
            height: 50rem;
        }

        .Test-text {
            font-size: 2rem;
            padding-bottom: 1rem;
            margin-bottom: 0;
        }

        .feedback {
            height: 10rem;
        }

        .des {
            font-size: -0.2rem;
        }

        .ecllip3 {
            height: 40vw;
            width: 40vw;
            margin: 0 auto;
            padding: 0;
        }

        .mona {
            font-size: 0.5rem;
            margin-top: -2rem;
            height: auto;
        }

        .size {
            height: 38rem;
        }
    }
}


@media (max-width:700px) {
    .size {
        height: auto;
        padding-bottom: 2rem;
    }

    .des {
        font-size: .8rem;
    }

    .Test-size {
        padding-bottom: 2rem;
        height: 48rem;
    }
}

@media (max-width: 1250px) {

    .Test-size {
        height: 55rem;
    }
}